/* Footer.css */
.footer {
    background-color: #17292E;
    color: white;
    text-align: center;
    padding: 4rem 2rem; /* Adjust padding as needed */
    position: relative;
  }
  
  .footer__content p {
    display: inline;
    margin: 0;
    padding: 0;
    font-size: 1.4rem;
  }

  .footer__content img {
    width: 50px;
    height: 50px;
    transform: translate(0, 13px)
    
  }
